import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	computed: {
		...mapState(['splashAdvertPreTime']),
	},
	methods: {
		...mapMutations(['login', 'saveUserInfo'])
	},
	onLaunch: function() {
		//重置登陆状态
		uni.getStorage({
			key: 'userToken',
			success: (res) => {
				uni.getStorage({
					key: 'userInfo',
					success: (res) => {
						this.login();
						this.saveUserInfo(res.data);
					}
				})
			}
		});	
		uni.getSystemInfo({
			success: (res) => {
				uni.setStorageSync('systemInfo', res);
			}
		})
		//获取客户端访问接口token
		this.$ajax.ajaxFunctions.getClientToken();
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	},
}
